.card-img,
.card-img-top {
  border-top-left-radius: calc(0rem - 1px);
  border-top-right-radius: calc(0rem - 1px);
}

.Container {
  padding: 0 79px;
}

.newsHeadingContainer {
  align-items: center;
  padding: 100px 79px;
}


.newsHeadingContainer h1 {
  font-family: NeueMachina-Ultrabold;
  font-style: normal;
  font-weight: 800;
  font-size: clamp(1.875rem, 5vw, 4.375rem);
  line-height: 110%;
  text-transform: capitalize;
  color: #ffffff;
}

.newsHeadingContainer p {
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: clamp(1rem, 5vw, 1.25rem);
}

.newsHeadingContainer p:hover {
  background: linear-gradient(90.62deg, #6d6bdb 0.53%, #1d1c4e 99.59%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media screen and (max-width: 768px) {
  .newsHeadingContainer h1 {
    margin: 50px 0;
  }
}

.Card {
  background: none;
  border-radius: none;
}

.Cover {
  margin: 10px 0 0 0;
}

.Category {
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  text-transform: uppercase;
  font-size: 25px;
  line-height: 153.5%;
  color: #fff;
  padding-top: 15px;
}

.Cover h3 {
  font-family: NeueMachina-Ultrabold;
  font-style: normal;
  font-weight: 800;
  font-size: clamp(1.125rem, 5vw, 1.375rem);
  line-height: 153.5%;
  color: #fff;
}

.news-body p {
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  text-transform: uppercase;
  font-size: clamp(1rem, 5vw, 1.25rem);
  line-height: 153.5%;
  color: #8d8d8d;
}

@media screen and (max-width: 768px) {
  .Container {
    padding: 30px;
  }
  
  .newsHeadingContainer {
    padding: 50px 30px;
  }
}

@media screen and (max-width: 375px) {

  .newsHeadingContainer {
    padding: 0 30px;
  }
}