.blogDetailsTop {
  margin-top: 50px;
}

.backToBlog {
  display: flex;
}

.backToBlog .backIcon,p
 {
  color: #fff;
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: clamp(1rem, 5vw, 1.5rem);
  line-height: 23px;
  padding-right: 5px;
}

.blogDetailsImage img {
  margin: 10px 0 0 0;
  width: 100%;
  height: 400px;
}

@media screen and (max-width: 320px) {

  .blogDetailsImage img {
  
   height:200px;
  }
  
  
}
.blogTittle h1 {
  font-family: NeueMachina-Ultrabold;
  font-style: normal;
  font-weight: 800;
  font-size: clamp(1.563rem, 5vw, 3.125rem);
  line-height: 153.5%;
  color: #fff;
  margin-top: 20px;
}

.authorShare {
  margin-top: 30px;
  margin-bottom: 30px;
  position: relative;
}

.authorContainer {
  display: flex;
  align-items: center;
}

.authorImage img {
  border-radius: 100%;
  width: clamp(5rem, 5vw, 7.5rem);
  /* height: clamp(5rem, 5vh, 7.5rem); */
  height: auto;
}

.authorNameBlogDate {
  margin-left: 20px;
}

.authorNameBlogDate h3 {
  font-family: NeueMachina-Ultrabold;
  font-style: normal;
  font-weight: 800;
  font-size: clamp(1.25rem, 5vw, 1.5rem);
  line-height: 23px;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  color: #fff;
}

.authorNameBlogDate p {
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: clamp(1rem, 5vw, 1.375rem);
  line-height: 153.5%;
  text-transform: uppercase;
  color: #8d8d8d;
}

.shareIconContainer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: #fff;
}

.shareIconContainer button {
    background: none;
    border: none;
}

.shareIconContainer button:active {
    background: none;
    border: none;
    outline: none;
}

.shareIcon {
  font-size: clamp(1.25rem, 5vw, 1.875rem);
  color: #fff;
  margin-left: 20px;
}

/* Blog Details Content CSS Start */

.blogDetailsContent {
  border: 1px solid #fff;
}

.blogContent {
  padding: 2rem;
  text-align: justify;
}

.blogContent p {
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 153.5%;
  text-align: justify;
  white-space: pre-wrap;
}

.blogContent h3 {
  font-size: clamp(1.25rem, 5vw, 1.5rem);
  color: #fff;
  font-family: NeueMachina-Ultrabold;
  font-style: normal;
  font-weight: 800;
}

/* Comment section CSS Start */

.commentContainer {
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff;
  padding: 2rem;
}

.commentHr {
  border: 1px solid #fff;
}

.commentArea {
  display: flex;
  justify-content: space-between;
}

.commentContainer .CommentImgInput img {
  border-radius: 100%;
  width: clamp(2.5rem, 5vw, 5rem);
}

.CommentImgInput {
  margin-top: 20px;
}

.commentContainer .CommentImgInput input {
  background: rgba(255, 255, 255, 0.34);
  border: none;
  padding: 20px;
  width: 92%;
  margin-left: 20px;
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: clamp(1rem, 5vw, 1.375rem);
  line-height: 110%;
  color: #fff;
}

.commentContainer .CommentImgInput input:focus {
  outline: none;
}

@media screen and (max-width: 768px) {
  .commentContainer {
    margin-top: 0;
  }

  .commentContainer .CommentImgInput input {
    width: 82%;
  }

  .blogContent {
    padding: 1rem;
  }
}

@media screen and (max-width: 375px) {
  .commentContainer .CommentImgInput input {
    width: 75%;
  }

  .blogContentSection {
    margin: 0 1rem;
  }
  .blogCommentSection {
    margin: 0 1rem;
  }
}