.headingContainer h1 {
  font-family: NeueMachina-Ultrabold;
  font-style: normal;
  font-weight: 800;
  font-size: clamp(1.875rem, 5vw, 4.375rem);
  line-height: 110%;
  text-transform: capitalize;
  color: #ffffff;
  margin: 100px 0 50px 79px;
}

.react-multi-carousel-list {
  display: flex !important;
  align-items: center !important;
  overflow: hidden !important;
  position: relative !important;
  padding: 0 79px !important;
}
.date {
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 140%;
  color: #C2C2C2;
  margin: 10px 0;
}
.YtSliderTitle p {
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: clamp(1.25rem, 5vw, 2.5rem);
  line-height: 120%;
  color: #fff;
}
.youtubeiframe {
  width: 500px;
  height: 300px;
}
.youtubeiframe iframe {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media screen and (max-width:768px) {
  .react-multi-carousel-list {
    display: flex !important;
    align-items: center !important;
    overflow: hidden !important;
    position: relative !important;
    padding: 0 30px !important;

  }
  .headingContainer h1 {
    margin: 70px 0 50px 30px;
  }
  .youtubeiframe {
    width: 300px;
    height: 100px;
  }
  .youtubeiframe iframe {
    width: 100%;
    height: 120%;
    object-fit: cover;
  }
  .YtSliderTitle p {
    font-family: NeueMachina-Regular;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 120%;
    color: #fff;
    padding-right: 20px;
  }
}
@media screen and (min-width: 769px) and (max-width: 1580px) {
  .youtubeiframe {
    width: 350px;
    height: 200px;
  }
}
@media screen and ( max-width: 430px) {
  .youtubeiframe {
    width: 350px;
    height: 150px;
  }
}
@media screen and (min-width: 431px) and (max-width: 635px) {
  .youtubeiframe {
    width: 520px;
    height: 200px;
  }
  .YtSliderTitle p {
    font-family: NeueMachina-Regular;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 120%;
    color: #fff;
    padding-right: 50px;
    white-space: pre-wrap;
  }
}