.desktopContainer {
  position: Relative;
  padding: 10px 100px;
}

@media screen and (max-width: 1024px) {
    .desktopContainer {
        display: none;
      }
}

.logoContainer {
  display: inline-block;
  width: 250px;
  height: auto;
  padding: 20px 0 20px 0;
}

.logoContainer h1 {
  font-family: NeueMachina-Ultrabold;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 17px;
  letter-spacing: 0.15em;
  text-transform: capitalize;
  color: #ffffff;
}

.logoContainer p{
  font-family: Mandali-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  text-transform: capitalize;
  color: #ffffff;
}


.header_menu {
    text-align: center;
  }
  
  .header_menu ul li {
    position: relative;
    display: inline-block;
    margin-right: 30px;
  }
  
  .header_menu ul li a {
    font-family: NeueMachina-Regular;
    color: #fff;
    font-size: 25px;
    display: block;
    padding: 20px 0 10px 0;
    position: relative;
  }
  
  .header_menu ul li a:hover {
    color: #6d6bdb;
    transition: all 0.5s ease-out;
  }
  
  .header_menu ul li a:after {
    position: absolute;
    content: "";
    left: 0;
    bottom: 0;
    width: 100%;
    height: 3px;
    background-color: #6d6bdb;
    transition: all 0.5s;
    transform: scale(0);
  }
  
  .header_menu ul li:hover a:after {
    transform: scale(1);
  }

  .header_menu ul .active a {
  color: #6d6bdb;
  border-bottom: 3px solid #6d6bdb;
  border-width: 50%;
  font-family: NeueMachina-Ultrabold;
  font-style: normal;
  font-weight: 800;
}