.fixedYtContainer {
  font-size: 60px;
  position: fixed;
  top: 50%;
  right: 0;
  background: linear-gradient(180deg, #464596 0%, #030b29 100%);
  color: white;
  border-radius: 245px;
  padding: 10px;
  border: 1.59px solid;
  border: 1.59px solid;
  border-image-source: linear-gradient(
      180deg,
      #0a0a0a 0%,
      #ffffff 54.22%,
      rgba(2, 2, 2, 0.96) 100%
    )
    1;
  border-radius: 0px 0px 63.7222px 63.7222px;
  transform: rotate(90deg);
  z-index: 999;
}

.fixedYtContainer .YtIcon {
    transform: rotate(270deg);
    color: #fff;
}