.contactMainSection {
  background: url(../../assets//img/bgMapImage.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  padding: 30px 0;
}

.contactHeadingContainer h1 {
  display: flex;
  justify-content: center;
  font-family: NeueMachina-Ultrabold;
  font-style: normal;
  font-weight: 800;
  font-size: clamp(1.875rem, 5vw, 4.375rem);
  line-height: 110%;
  text-transform: capitalize;
  color: #ffffff;
}

.contactMailContainer a {
  display: flex;
  justify-content: center;
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: clamp(1.125rem, 5vw, 1.5rem);
  line-height: 110%;
  color: #ffffff;
}

/* ///// inputs /////*/

input:focus ~ .contactlabel,
textarea:focus ~ .contactlabel,
input:valid ~ .contactlabel,
textarea:valid ~ .contactlabel {
  font-size: 0.75em;
  color: #999;
  top: -5px;
  -webkit-transition: all 0.225s ease;
  transition: all 0.225s ease;
}

.styled-input {
  float: left;
  width: 293px;
  margin: 1rem 0;
  position: relative;
  border-radius: 4px;
}

.styled-input input {
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: clamp(1rem, 5vw, 1.875 rem);
  line-height: 110%;
}

.styled-input input:focus,
textarea:focus {
  border: 1px solid #6d6bdb;
}

.styled-input label {
  color: #999;
  padding: 1.3rem 30px 1rem 30px;
  position: absolute;
  top: 10px;
  left: 0;
  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;
  pointer-events: none;
}

.styled-input.wide {
  width: 650px;
  max-width: 100%;
}

input,
textarea {
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: clamp(1rem, 5vw, 1.875 rem);
  line-height: 110%;
  padding: 30px;
  border: 0;
  width: 100%;
  font-size: 1rem;
  background: none;
  color: white;
  border: 1px solid #fff;
  border-radius: 4px;
}

input:focus,
textarea:focus {
  outline: 0;
}

input:focus ~ span,
textarea:focus ~ span {
  width: 100%;
  -webkit-transition: all 0.075s ease;
  transition: all 0.075s ease;
}

textarea {
  width: 100%;
  min-height: 15em;
}

.input-container {
  width: 650px;
  max-width: 100%;
  margin: 50px auto 50px auto;
  background: #0e0e16;
  padding: 20px 10px;
}

input[type="checkbox"] + label {
  color: #ccc;
  font-style: italic;
}

input[type="checkbox"]:checked + label {
  color: #f00;
  font-style: normal;
}

.send {
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: clamp(1rem, 5vw, 1.875 rem);
  line-height: 110%;
  border: 1px solid #fff;
  background: none;
  outline: none;
  color: #fff;
  padding: 20px;
  width: 249px;
}

.send:focus {
  outline: 1px solid #6d6bdb;
}

@media only screen and (max-width: 768px) {
  .styled-input {
    width: 100%;
  }

  .send {
    width: 100%;
  }
}
