.aboutDjContainer {
    padding: 0 79px 30px 79px;
  }

  @media screen and (max-width: 768px) {
    .aboutDjContainer{
        padding: 0 30px;
    }
  }
  
  .aboutDjWrapper {
    margin: 100px 0 0 0;
  }
  
  .aboutDjWrapper img {
    width: 100%;
  }
  
  .aboutDjContainer h1 {
    font-family: NeueMachina-Ultrabold;
    font-style: normal;
    font-weight: 800;
    font-size: clamp(1.875rem, 5vw, 4.375rem);
    line-height: 68px;
    color: #ffffff;
  }
  
  .aboutDjContainer p {
    font-family: NeueMachina-Regular;
    font-style: normal;
    font-weight: normal;
    font-size: clamp(1rem, 5vw, 1.375rem);
    line-height: 123%;
    text-align: justify;
    color: #ffffff;
  }
  
  .aboutDjContainer h3 {
    font-family: NeueMachina-Ultrabold;
    font-style: normal;
    font-weight: 800;
    font-size: clamp(1rem, 5vw, 1.5rem);
    line-height: 123%;
    color: #ffffff;
  }
  
  .aboutDjContainer ul li {
      font-family: NeueMachina-Regular;
      font-style: normal;
      font-weight: normal;
      font-size: clamp(1rem, 5vw, 1.375rem);
      line-height: 123%;
      text-align: justify;
      color: #ffffff;
    }
  