.Wrapper {
  margin: 200px 0 0 79px;
  z-index: 10;
}

@media screen and (max-width: 768px) {
  .Wrapper {
    margin: 50px 30px;
    text-align: center;
    align-items: center;
  }
}

@media screen and (min-width: 769px) and (max-width: 1024px) {
  .Wrapper {
    margin: 50px 30px;
    text-align: center;
    align-items: center;
  }
}

.Title h3 {
  font-family: NeueMachina-Ultrabold;
  font-style: normal;
  font-weight: 800;
  font-size: clamp(2.5rem, 5vw, 6rem);
  line-height: 93px;
  background: linear-gradient(90.62deg, #6d6bdb 0.53%, #1d1c4e 99.59%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.SubTitle p {
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: 56px;
  line-height: 60px;
  color: #ffffff;
  transform: rotate(0.08deg);
  margin: 20px 0 0 0;
}

@media screen and (max-width: 480px) {
  .SubTitle p {
    font-size: 30px;
    line-height: 40px;
    margin: 0;
  }
}

.Description p {
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 30px;
  color: #fff;
  margin: 20px 0 0 0;
}

@media screen and (max-width: 480px) {
  .Description p {
    font-size: 18px;
    line-height: 20px;
    margin: 10px 0 0 0;
  }
}

.button {
  position: relative;
  border: 1px solid transparent;
  border-radius: 124.643px;
  background: rgb(0, 0, 0);
  background-clip: padding-box;
  padding: 20px 25px;
  font-size: 24px;
  color: #fff;
  font-family: NeueMachina-Ultrabold;
  font-style: normal;
  font-weight: 800;
  margin: 30px 0 0 0;
}

.button::after {
  position: absolute;
  top: -2px;
  bottom: -2px;
  left: -2px;
  right: -2px;
  background: linear-gradient(92.58deg, #1d1d3a 1.49%, #6b69d8 98.75%);
  content: "";
  z-index: -1;
  border-radius: 124.643px;
}

.button:hover {
  border: none;
  background: linear-gradient(92.58deg, #1d1d3a 1.49%, #6b69d8 98.75%);
  transition: all 0.5s ease-in-out;
}

.button::after .button:hover {
  transform: translate(1);
  transition: all 0.5s ease-in-out;
}

