.tsMainContainer {
  margin-bottom: 30px;
}

.tsScroll {
  max-height: 370px;
  overflow-y: scroll;
}

.tsHeadingContainer {
  align-items: center;
  padding: 100px 79px;
}

.tsHeadingContainer h1 {
  font-family: NeueMachina-Ultrabold;
  font-style: normal;
  font-weight: 800;
  font-size: clamp(1.875rem, 5vw, 4.375rem);
  line-height: 110%;
  text-transform: capitalize;
  color: #ffffff;
}

@media only screen and (max-width: 768px) {
  /* .container {
    margin-top: 66px;
  } */

  .tsHeadingContainer {
    padding: 50px 30px;
  }

  .testimonial-list {
    padding: 0;
  }
}

@media screen and (max-width: 375px) {

  .tsHeadingContainer  {
    padding: 30px;
  }
}

/* .height {
    height: 100vh
} */

.card {
  border: none;
  cursor: pointer;
  box-shadow: 0 0 40px rgba(255, 255, 255, 0.1);
  width: 100%;
  background: #000000;
  padding: 50px;
  font-family: NeueMachina-Ultrabold;
}

.customer {
  font-family: NeueMachina-Regular;
}

.card:hover {
  background-color: rgb(214, 214, 214);
  color: #000;
}

.card:active, .card:focus, .card:focus-visible {
  background-color: rgb(214, 214, 214) !important;
  color: #000;
}

.testimonial-list {
  list-style: none;
}

.testimonial-list li {
  margin-bottom: 20px;
  display: flex;
}

.testimonials-margin {
  margin-top: -19px;
}

.tsTitle {
  font-family: NeueMachina-Ultrabold;
  font-style: normal;
  font-weight: 800;
  font-size: clamp(1.875rem, 5vw, 3rem);
  line-height: 110%;
  text-transform: capitalize;
  color: #ffffff;
  text-align: justify;
}
.tsRating {
  color: orange;
  padding: 10px 0;
  font-size: 30px;
}

.TestiContent {
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: clamp(1rem, 5vw, 1.54rem);
  line-height: 110%;
}
