* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

p {
  font-size: clamp(1rem, 5vw, 1.375rem);
}
h3, h4 {
  font-size: clamp(1.25rem, 5vw, 1.5rem);
  color: #fff;
  font-family: NeueMachina-Ultrabold;
  font-style: normal;
  font-weight: 800;
}

a,
a:hover,
a:focus {
  text-decoration: none;
  outline: none;
}

body {
  background-color: #000;
}

::-webkit-scrollbar {
  width: 0.7em;
  height: 8px
}

::-webkit-scrollbar-thumb {
  cursor: pointer;
  background: linear-gradient(-90.66deg, #030B29 0.57%, #464596 49.45%, #030B29 97.33%);
  border-radius: 100px;
}

