.blogTopSection {
  margin: 30px 79px;
}

@media screen and (max-width: 768px) {
  .blogTopSection {
    margin: 30px;
  }
  .jensBlogTopImg {
    display: none;
  }
}

.topBlogContent {
  display: grid;
  place-content: center;
}

.topBlogContent h1{
  font-style: normal;
  font-family: NeueMachina-Ultrabold;
  font-weight: 800;
  font-size: clamp(2.5rem, 5vw, 6rem);
  line-height: 93px;
  text-transform: capitalize;
  color: #fff;
}

@media screen and (max-width: 768px) {
  .topBlogContent h1 {
    line-height: 50px;
  }
  .jensBlogTopImg img {
    display: none;
  }
}

.topBlogContent span {
  background: linear-gradient(90.62deg, #6d6bdb 0.53%, #242268 99.59%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.topBlogContent p {
  font-style: normal;
  font-family: NeueMachina-Regular;
  font-weight: 800;
  font-size: clamp(1rem, 5vw, 1.125rem);
  line-height: 30px;
  text-transform: capitalize;
  text-align: justify;
  color: #fff;
  margin-top: 20px;
}
