.copiedMain {
    position: relative;
}

.copiedContainer {
  background: #575757;
  color: #fff;
  width: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  position: absolute;
  right: 0;
  bottom: 15px;
  padding: 15px;
}

.copiedContainer p {
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: clamp(0.7rem, 5vw, 1rem);
  margin-bottom: 0;
}
