.productHeadingContainer {
    align-items: center;
    padding: 100px 79px;
  }
  
  
  .productHeadingContainer h1 {
    font-family: NeueMachina-Ultrabold;
    font-style: normal;
    font-weight: 800;
    font-size: clamp(1.875rem, 5vw, 4.375rem);
    line-height: 110%;
    text-transform: capitalize;
    color: #ffffff;
  }

  
  .homeProductContainer {
    padding: 0 79px;
  }
  
  .product-grid {
    background-color: none;
    font-family: NeueMachina-Ultrabold;
    text-align: center;
  }
  .product-grid .product-image {
    overflow: hidden;
    position: relative;
  }
  .product-grid .product-image a.image {
    display: block;
  }
  .product-grid .product-image img {
    width: 100%;
    height: auto;
  }
  .product-grid .product-image .pic-1 {
    transition: all 0.3s ease 0s;
  }
  .product-grid .product-image:hover .pic-1 {
    transform: translateX(100%);
  }
  .product-grid .product-image .pic-2 {
    width: 100%;
    height: 100%;
    transform: translateX(-101%);
    position: absolute;
    top: 0;
    left: 0;
    transition: all 0.3s ease 0s;
  }
  .product-grid .product-image:hover .pic-2 {
    transform: translateX(0);
  }
  .product-grid .product-sale-label {
    color: #fff;
    background: #0a805e;
    font-size: 13px;
    text-transform: uppercase;
    letter-spacing: 1px;
    padding: 2px 8px;
    position: absolute;
    top: 15px;
    left: 15px;
  }
  .product-grid .product-like-icon {
    color: #696969;
    font-size: 22px;
    line-height: 20px;
    position: absolute;
    top: 15px;
    right: 15px;
  }
  .product-grid .product-like-icon:hover {
    color: #0a805e;
  }
  .product-grid .product-like-icon:before,
  .product-grid .product-like-icon:after {
    content: attr(data-tip);
    color: #fff;
    background-color: #000;
    font-size: 12px;
    line-height: 18px;
    padding: 7px 7px 5px;
    visibility: hidden;
    position: absolute;
    right: 0;
    top: 15px;
    transition: all 0.3s ease 0s;
  }
  .product-grid .product-like-icon:after {
    content: "";
    height: 15px;
    width: 15px;
    padding: 0;
    transform: translateX(-50%) rotate(45deg);
    right: auto;
    left: 50%;
    top: 15px;
    z-index: -1;
  }
  .product-grid .product-like-icon:hover:before,
  .product-grid .product-like-icon:hover:after {
    visibility: visible;
    top: 30px;
  }
  .product-grid .product-links {
    width: 170px;
    padding: 0;
    margin: 0;
    list-style: none;
    opacity: 0;
    transform: translateX(-50%);
    position: absolute;
    bottom: -50px;
    left: 50%;
    transition: all 0.3s ease 0s;
  }
  .product-grid:hover .product-links {
    bottom: 40px;
    opacity: 1;
  }
  .product-grid .product-links li {
    display: inline-block;
    margin: 0 2px;
  }
  .product-grid .product-links li a {
    color: #fff;
    background: #000;
    font-size: 16px;
    line-height: 48px;
    width: 48px;
    height: 48px;
    border-radius: 10px;
    display: block;
    transition: all 0.3s ease 0s;
  }
  .product-grid:hover .product-links li a:hover {
    background: #333;
  }
  .product-grid .product-content {
    text-align: left;
    padding: 15px 0 0;
  }
  
  .product-grid .category {
      font-family: NeueMachina-Regular;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 153.5%;
      text-transform: uppercase;
      color: #fff;
  }
  .product-grid .title {
    font-style: normal;
    font-weight: 800;
    font-size: 22px;
    line-height: 153.5%;
    text-transform: capitalize;
  }
  .product-grid .title a {
    color: rgb(255, 255, 255);
    transition: all 0.3s ease 0s;
  }
  .product-grid .title a:hover {
    background: linear-gradient(90.62deg, #6d6bdb 0.53%, #1d1c4e 99.59%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  .product-grid .price {
    color: #8d8d8d;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 153.5%;
  }
  .product-grid .price span {
    color: #555;
    font-size: 14px;
    font-weight: 400;
    text-decoration: line-through;
    margin: 0 5px 0 0;
  }
  @media screen and (max-width: 990px) {
    .product-grid {
      margin: 0 0 30px;
    }
  }
  
  @media screen and (max-width: 768px) {
    
    .productHeadingContainer {
      padding: 50px 30px;
    }
  
    .productHeadingContainer h1 {
      margin: 50px 0;
    }
  
    .homeProductContainer {
      padding: 30px;
    }
    
  }
  
  @media screen and (max-width: 375px) {
  
    .productHeadingContainer  {
      padding: 0 30px;
    }
  }