.categoryContainer {
  display: flex;
  align-items: center;
  padding: 50px 79px 10px 79px;
}

@media screen and (max-width: 768px) {
  .categoryContainer {
    padding: 20px 30px;
  }
}

.allPost {
  background: none;
  border: none;
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: clamp(1.25rem, 5vw, 1.563rem);
  line-height: 24px;
  text-transform: capitalize;
  color: #ffffff;
  margin-right: 30px;
}

.dropbtn {
  background: none;
  border: none;
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: clamp(1.25rem, 5vw, 1.563rem);
  line-height: 24px;
  text-transform: capitalize;
  color: #ffffff;
}

.dropdown {
  position: relative;
  display: inline-block;
  width: 50%;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: rgb(29, 29, 29);
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  color: #fff;
}

.dropdown-content a {
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  padding: 1rem;
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: clamp(1.25rem, 5vw, 1.563rem);
  line-height: 24px;
  text-transform: capitalize;
  color: #ffffff;
}

.dropdown-content a:hover {
  background-color: #ddd;
  color: #000;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown:hover .dropbtn {
  background-color: #000;
}
