.aboutJensContainer {
  padding: 0 79px;
}

@media screen and (max-width: 768px) {
    .aboutJensContainer{
        padding: 0 30px;
    }
  }

.aboutJensWrapper {
  margin: 30px 0 0 0;
}

.aboutJensWrapper img {
  width: 100%;
}

.aboutJensContainer h1 {
  font-family: NeueMachina-Ultrabold;
  font-style: normal;
  font-weight: 800;
  font-size: clamp(1.875rem, 5vw, 4.375rem);
  line-height: 68px;
  color: #ffffff;
}

.aboutJensContainer p {
  font-family: NeueMachina-Regular;
  font-style: normal;
  font-weight: normal;
  font-size: clamp(1rem, 5vw, 1.375rem);
  line-height: 123%;
  text-align: justify;
  color: #ffffff;
}

.aboutJensContainer h3 {
  font-family: NeueMachina-Ultrabold;
  font-style: normal;
  font-weight: 800;
  font-size: clamp(1rem, 5vw, 1.5rem);
  line-height: 123%;
  color: #ffffff;
}

.aboutJensContainer ul li {
    font-family: NeueMachina-Regular;
    font-style: normal;
    font-weight: normal;
    font-size: clamp(1rem, 5vw, 1.375rem);
    line-height: 123%;
    text-align: justify;
    color: #ffffff;
  }
